.header {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    padding: 16px 0;
    width: 100%;
    background-color: #1b1e21;
    margin-bottom: 25px;
    height: 62px;

    &__title {
        color: #fff;
        font-size: 25px;
        margin: 0;
        padding: 0;
    }
}
